.modal {
  position: absolute;
  display: none;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  top: 0;
  left: 0;
  width: 100%;
  height: 100vh;
  background-color: rgba(0, 0, 0, 0.4);
}

.modal__body {
  position: relative;
  width: 650px;
  height: 450px;
  background-color: #fff;
  border-radius: 5px;
  text-align: center;
  padding: 10px;
}

.modal__body__close {
  position: absolute;
  top: 10px;
  right: 10px;
  font-size: 20px;
  cursor: pointer;
}

.modal__body ul {
  list-style: none;
  text-align: left;
}
